import {
  ErrorHandlerError,
  MinimalHttpError,
} from '@wix/error-handler-core/types';

export function responseToMinimalHttpError(
  err: Response & ErrorHandlerError,
): MinimalHttpError {
  const data = err._errorHandlerState?.responseData;
  return {
    response: {
      data,
      status: err.status,
    },
    requestId: err.headers.get('X-Wix-Request-Id') as string | undefined,
  };
}
